export enum BaseType {
    String = 'string',      // 
    Number = 'number',      // 
    Boolean = 'boolean',    // 
    Array = 'array',        // 
    Object = 'object',      // 
}

export interface BaseND {
    name: string
    desc: string
}

/*
    stringitemsObject
    arrayitemsarrayObject
 */
export interface BaseStruct {
    name: string            // 
    desc: string            // 
    value?: string          // 
    must?: boolean          // false
    type?: string           // string
    arrayType?: BaseType    // 
    tips?: BaseND[]         // 
    items?: BaseStruct[]    // 
}

export interface Point extends BaseStruct {
    id: number
    label: string
    mark: string
    children: Point[]
}

const RootPoint: string = 'root'
const KindPoint: string = 'kind'
export {RootPoint, KindPoint}
