import Pod from '@/data/pod'
import Deployment from '@/data/deployment'
import Service from '@/data/service'
import {BaseStruct, BaseType} from "@/service/base";

/**
 * 
 * @return BaseStruct
 */
function buildDefaultArrayStruct(): BaseStruct {
    return <BaseStruct>{
        name: "-", desc: "", value: "",
        type: BaseType.String,
        must: true,
        arrayType: undefined,
        items: [], tips: []
    }
}

/**
 * 
 * @param base
 * @return BaseStruct[]
 */
function baseToFullBase(base: BaseStruct[]): BaseStruct[] {
    let full: BaseStruct[] = []
    for (const v of base) {
        // must
        v.must = v.must || false
        // value
        v.value = v.value || ''
        // tips
        v.tips = v.tips || []
        // items
        v.items = v.items || []
        // items &&  => stringitems &&  => object
        const itemsLen = v.items.length
        if (itemsLen > 0 && !v.type) {
            v.type = BaseType.Object
        }
        v.type = v.type || BaseType.String
        // array && items => object
        if (v.type === BaseType.Array) {
            let arrayExist: boolean = false
            if (itemsLen > 0 && !v.arrayType) {
                v.arrayType = BaseType.Object
                if (v.items[0].name === '-') {
                    arrayExist = true
                }
            }
            v.arrayType = v.arrayType || BaseType.String
            //  TODO 
            if (!arrayExist) {
                const arrayStruct = buildDefaultArrayStruct()
                arrayStruct.type = v.arrayType
                arrayStruct.items = baseToFullBase(v.items)
                arrayStruct.tips = v.tips
                v.items = [arrayStruct]
            }
        } else if (itemsLen > 0) {
            v.items = baseToFullBase(v.items)
        }
        full.push(v)
    }
    return full
}

const baseK8s = [{
    "name": "root",
    "desc": "",
    "type": "object",
    "must": true,
    "items": [{
        "name": "apiVersion",
        "desc": "v1 kubectl api-versions ",
        "must": true,
        "tips": [{
            "name": "v1",
            "desc": "v1"
        }]
    }, {
        "name": "kind",
        "desc": "Pod",
        "must": true,
        "tips": [{
            "name": "Service",
            "desc": ""
        }, {
            "name": "Deployment",
            "desc": ""
        }, {
            "name": "Pod",
            "desc": ""
        }]
    }]
}]

const all: { [index: string]: BaseStruct[] } = {baseK8s, Pod, Deployment, Service}
for (const index in all) {
    all[index] = baseToFullBase(all[index])
}
export default all
